"use client"

import { useEffect, useState } from "react"
import { useRouter } from "next/navigation"
import LearningDashboard from "@/components/learning-dashboard"
import SentencePracticeApp from "@/components/sentence-practice-app"
import { supabase } from "@/lib/supabase"

export default function Home() {
  const router = useRouter()
  const [showLesson, setShowLesson] = useState(false)
  const [currentCourseId, setCurrentCourseId] = useState<string>("daily-conversation")
  const [checkingAuth, setCheckingAuth] = useState(true)

  // ÁÆÄÂçïÁöÑÂÆ¢Êà∑Á´ØË∑ØÁî±‰øùÊä§ÔºöÂ¶ÇÊûúÊ≤°ÊúâÁôªÂΩïÂàôË∑≥ËΩ¨Âà∞ /login
  useEffect(() => {
    const checkSession = async () => {
      const {
        data: { session },
      } = await supabase.auth.getSession()

      if (!session) {
        router.push("/login")
      } else {
        setCheckingAuth(false)
      }
    }

    checkSession()
  }, [router])

  if (checkingAuth) {
    return (
      <div className="min-h-screen flex items-center justify-center bg-slate-50 text-slate-500">
        Ê≠£Âú®Ê£ÄÊü•ÁôªÂΩïÁä∂ÊÄÅ...
      </div>
    )
  }

  if (showLesson) {
    return (
      <SentencePracticeApp
        courseId={currentCourseId}
        onBack={() => setShowLesson(false)}
        onComplete={(stats) => {
          console.log("Course completed:", stats)
          // ËøôÈáåÂèØ‰ª•‰øùÂ≠òÂ≠¶‰π†ËøõÂ∫¶Âà∞ localStorage ÊàñÂèëÈÄÅÂà∞ÊúçÂä°Âô®
        }}
      />
    )
  }

  return (
    <LearningDashboard
      onStartLesson={(courseId) => {
        setCurrentCourseId(courseId)
        setShowLesson(true)
      }}
    />
  )
}
